idth: 380px){form .checkbox span{font-size:9px}}form .btn{display:flex}form .btn button{width:100%;background:#1570ef;cursor:pointer;border:none;color:#fff;box-shadow:0px 1px 2px rgba(16,24,40,.05);border-radius:8px;border:1px solid #1570ef;padding:12px 20px}form .btn button:hover{background:#074eb3}label{font-family:"Inter";font-style:normal;font-weight:500;font-size:14px;line-height:20px}input,textarea{border-radius:8px;border:1px solid #d0d5dd;padding:.5rem 10px;font-family:"Inter";font-style:normal;font-weight:500;margin-top:10px;outline:none}input:focus,textarea:focus{border:1px solid #1570ef}